.Form {
    width: 384px;
    height: 650px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 20px;
    padding: 0 32px 0 32px;
}

.Title {
    font-size: 2em;
    color: #515a63;
    font-weight: 900;
    line-height: 1.4em;
    margin-top: 84px;
    margin-bottom: 0;
    text-align: left;
    width: 100%;
}

.Description {
    color: #949a9f;
    font-weight: 500;
    width: 100%;
    margin-bottom: 36px;

}



.UrlPlaceholder {
    color: #afb6bb;
    font-weight: 500;
    width: 100%;
    margin-bottom: 64px;
    height: 40px;
    display: flex;
    align-items: center;
    user-select: none;
}


.ShortenedUrl {
    font-size: 1.5em;
    font-weight: 600;
    display: flex;
    align-items: flex-end;
    color: #000;
}

.Input {
    border: none;
    border-bottom: 2px solid rgba(219, 219, 219, 0.3);
    width: 100%;
    padding: 18px 0 10px 6px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;  
    font-size: 1em;
    background-color: #fff;
    box-shadow: none;
}


.Input::-webkit-input-placeholder {
    color: #9ca3a5;
    font-weight: 500;
    font-size: 1em;
}

.Input::-moz-placeholder {
    color: #9ca3a5;
    font-weight: 500;
    font-size: 1.2em;
}

.Input::-ms-input-placeholder { 
    color: #9ca3a5;
    font-weight: 500;
    font-size: 1.2em;
}

.Input:focus {
    outline: none;
    border-color: #4361ee;
}


.Link {
    display: flex;
    height: 40px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-bottom: 64px;
    margin-top: 16px;
}

.Image {
    width: 32px;
    height: 32px;
    margin-right: 16px;

}

.Button {
    width: 100%;
    height: 56px;
    background-color: #4361ee;
    border: none;
    border-radius: 10px;
    font-weight: 600;
    font-size: 1.1em;
    color: #fff;
    cursor: pointer;
}

.Result {
    width: 100%;
}


@media only screen and (max-width: 724px) {
    .Form {
      width: 70%;
      height: 600px;
    }
        .Title {
            font-size: 1.5em;
            color: #515a63;
            font-weight: 900;
            line-height: 1.4em;
            margin-top: 64px;
            margin-bottom: 0;
        }
        
        .Description {
            margin-bottom: 48px;
        }
        
        
        .UrlPlaceholder {
            font-weight: 500;
            width: 100%;
            margin-bottom: 72px;
            height: 40px;
        }
        
        
        .ShortenedUrl {
            width: 100%;
            font-size: 1em;
            font-weight: 600;
            height: 40px;
            /* margin-top: 16px; */
        }
        
        .Link {
            align-items: flex-end;
        }
        
        .Input {
            font-size: 1em;
        }
    }